.btn {
  cursor: pointer;
  outline: none;
  border: none;
  padding: 1.25em 2em;
  font-weight: var(--font-normal);
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--txt-heading-color);
  transition: 0.3s all;
  border-radius: 4px;
  font-weight: var(--font-bold);

  &:focus {
    outline: none;
    box-shadow: 0 0 0 3px var(--border-hover-color);
  }

  // options
  &-small {
    padding: 0.75em 2em;
    font-size: 0.875rem;
  }
  &-full {
    max-width: 100%;
    width: 100%;
  }
  &-rounded {
    border-radius: 5em;
  }

  // variants
  &-primary {
    background-color: var(--orange);
    color: white;
  }
}
