/* globals (colors, breakpoints, fonts ... etc) */

@use "./variables";
@use "./typography";

html,
body {
  font-size: 16px;
  font-family: var(--font-family);
  font-weight: var(--font-normal);
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -moz-osx-font-smoothing: grayscale;
}
