// colors
:root {
  --bg: #f4fafa;
  --blue: #0086ff;
  --orange: #ff7f45;
  --green: #1baba1;
  --dark: #2b3d41;
  --very-dark: #283639; // on hover

  // text color
  --txt-heading-color: #3d5056;
  --txt-normal-color: #9da9b7;

  // borders
  --border-color: #ced4da;
  --border-hover-color: hsla(176, 73%, calc(39% + 40%), 0.6);
  --icon-color: #7e8fa1;

  // font
  --font-family: "Poppins", sans-serif;
  --font-bold: 700;
  --font-normal: 400;
  --font-light: 300;
  --font-thin: 100;
}

