.field {
  // --icon-size: 28px;
  position: relative;
  display: block;
  width: 100%;
  // border: thin dotted lightcoral;
  margin: 0.75rem 0;

  // options
  &-rounded {
    .field__input {
      // border-radius: 8px;
    }
  }

  &__icon-left,
  &__icon-right,
  &__label {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    // border: thin dotted lightcoral;
    color: var(--icon-color);
  }

  &__icon-left {
    margin-left: 0.5rem;
    font-size: var(--icon-size);
    transition: 0.3s color;
  }

  // label
  &__label {
    transition: 0.3s all;
    padding-left: 0.75rem;
    font-weight: var(--font-normal);
  }

  // input
  &__input {
    font-weight: var(--font-normal);
    color: var(--txt-heading-color);
    z-index: 1;
    line-height: 1;
    appearance: none;
    outline: none;
    border: none;
    position: relative;
    width: 100%;
    background-color: transparent;
    padding: 1.625rem 0.75rem 0.75rem 0.75rem;
    border-radius: 4px;
    // border: 1px solid #ced4da;
    border: 1px solid var(--border-color);
    transition: 180ms box-shadow;
    &:focus {
      box-shadow: 0 0 0 0.25rem var(--border-hover-color);
      border-color: var(--green);
      outline: 3px solid transparent;
    }

    &:focus + label,
    &:not(:placeholder-shown) + label {
      display: block;
      background-color: white;
      top: 0;
      transform: translateY(0);
      padding-top: 0.5rem;
      font-size: 0.875rem;
    }

    &:focus + label,
    &:focus ~ span {
      color: var(--green);
    }

    &::placeholder {
      color: transparent !important;
      user-select: none !important;
    }
    &[type="password"] {
      letter-spacing: 2px;
    }
  }

  // password field
  &-password__toggle {
    span {
      font-size: 24px;
    }
    overflow: hidden;
    width: 5em;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 0.875rem;
    font-weight: var(--font-light);
    color: var(--icon-color);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.25rem;
    z-index: 2;
    cursor: pointer !important;
    border: none;
    background: none;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    padding: 0 0.75rem;
    &.active {
      outline: none;
      background-color: var(--green);
      color: white;
      border: 1px solid var(--green);
      // border-width: 1px;
      // border-style: solid;
      // border-color: var(--border-color) var(--border-color) var(--border-color)
      //   transparent;
      border-radius: 0 4px 4px 0;
    }
    &:focus {
      box-shadow: 0 0 0 0.25rem var(--border-hover-color);
      border-color: var(--green);
      outline: 3px solid transparent;
    }
  }
}
