$card-shadow-color: rgba(50, 50, 50, 0.45);

.card {
  background-color: white;
  color: var(--txt-heading-color);
  padding: 1rem;
  // box-shadow: 0px 0px 4px 0px $card-shadow-color;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  width: 100%;
  // login card

  &__header {
    // margin-bottom: 1rem;
  }
  &__title {
    font-size: 1.375rem;
    font-weight: bold;
    text-transform: capitalize;
  }
}

.card-login {
  .card__header {
    text-align: center;
    padding: 1.5rem 0;
  }
  .card__title {
    font-size: 1.75rem;
    margin-bottom: 1rem;
  }
  .card_subtext {
    font-size: 1rem;
    color: var(--txt-normal-color);
    font-weight: var(--font-light);
  }
}
